

































import Vue from 'vue'

export default Vue.extend({
  props: {
    routeNames: {
      type: Object,
      default () {
        return {}
      }
    },
    breadcrumbItems: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    const items: any[] = []
    const colorModes = [
      {
        mode: 'system',
        display: 'System'
      },
      {
        mode: 'light',
        display: 'Light'
      },
      {
        mode: 'dark',
        display: 'Dark'
      },
      {
        mode: 'sepia',
        display: 'Sepia'
      }
    ]
    return {
      colorModes,
      items
    }
  },
  methods: {
    setColorMode (mode: string) {
      this.$colorMode.preference = mode
    },
    getColorModeDisplay (mode: string) {
      return this.colorModes.find(p => p.mode === mode)?.display
    },
    getColorMode () {
      return this.$colorMode.preference
    }
  }
})
