














import Vue from 'vue'

Vue.directive('resize', {
  bind (el, binding) {
    let width: string | undefined = ''
    let height: string | undefined = ''
    function get () {
      const style = document.defaultView?.getComputedStyle(el)
      if (width !== style?.width || height !== style?.height) {
        binding.value({ width, height })
      }
      width = style?.width
      height = style?.height
    }

    (el as any).__vueReize__ = setInterval(get, 10)
  },
  unbind (el) {
    clearInterval((el as any).__vueReize__)
  }
})

export default Vue.extend({
  data () {
    const breadcrumbItems: any[] = []
    return {
      title: process.env.websiteName,
      breadcrumbItems,
      routeNames: {
        index: {
          text: '档案',
          to: '/'
        },
        observation: {
          text: '观测站',
          to: '/observation'
        },
        bar: {
          text: '吧台',
          to: '/bar'
        },
        brother: {
          text: '成员',
          to: '/brother'
        },
        game: {
          text: '合成大钢板',
          to: '/game'
        },
        treasury: {
          text: '藏宝阁',
          to: '/treasury'
        },
        about: {
          text: '关于我们',
          to: '/about'
        }
      }
    }
  },
  watch: {
    $route () {
      this.routeChange()
    }
  },
  mounted () {
    this.routeChange()
  },
  methods: {
    routeChange () {
      const currName = this.$route.name!
      const curr = this.routeNames[currName]
      const items = [
        {
          text: process.env.websiteName,
          to: '/'
        }
      ]
      items.push(curr)
      this.title = items.reverse().map(p => p.text).join(' - ')
      this.breadcrumbItems = items
    }
  },
  head () {
    const items = [process.env.websiteName]
    if (this.$route.name) {
      items.push(this.$data.routeNames[this.$route.name].text)
    }
    return {
      title: items.reverse().join(' - ')
    }
  }
})
